<script setup>
import { computed } from 'vue'
import Button from './UI/Button.vue'

const props = defineProps({
	total: {
		type: Number,
		required: true,
	},
	expenses: {
		type: Array,
		required: true,
	},
})

const isUserChoosePlata = props.expenses.some(
	item =>
		item.name == 'Заработная плата, не выплаченная пострадавшему' &&
		item.isChoose === true
)

const list = computed(() => {
	let temp = props.expenses.filter(
		item => item.correctValue !== null && item.isChoose === true
	)
	return temp.map(el => el.name)
})

const resultOne = computed(() => {
	return props.expenses.every(item => {
		return item.correctValue === null || item.value === item.correctValue
	})
})

const resultTwo = computed(() => {
	if (resultOne.value) return false

	const allCorrectChosen = props.expenses
		.filter(expense => expense.isCorrect)
		.every(expense => expense.isChoose)

	const noIncorrectChosen = props.expenses
		.filter(expense => !expense.isCorrect)
		.every(expense => !expense.isChoose)

	return allCorrectChosen && noIncorrectChosen
})

const resultThree = computed(() => {
	if (resultOne.value) return false
	if (resultTwo.value) return false
	if (isUserChoosePlata) return false
	return props.expenses.some(
		item => item.isChoose === true && item.isCorrect === true
	)
})

const resultFour = computed(() => {
	if (!resultOne.value && !resultTwo.value && !resultThree.value) {
		if (
			!props.expenses.some(
				item => item.isChoose === true && item.isCorrect === true
			)
		) {
			return false
		}
	}

	return isUserChoosePlata
})

const reload = () => {
	location.reload()
}
</script>

<template>
	<div class="container">
		<div class="text" v-if="resultOne">
			Подсчет экономических потерь работодателя - это сложный процесс, но вы
			отлично с ним справились! Вы верно учли затраты от несчастного случая:
		</div>
		<div class="text" v-if="resultTwo">
			Подсчет экономических потерь работодателя - это сложный процесс. Вы верно
			учли затраты от несчастного случая, но неверно посчитали суммы. Так, вы
			учли следующие потери:
		</div>
		<div class="text" v-if="resultThree">
			Не совсем верно. Подсчет экономических потерь работодателя - это сложный
			процесс. Вы верно учли следующие затраты:
		</div>
		<div class="text" v-if="resultFour">
			Не совсем верно. Подсчет экономических потерь работодателя - это сложный
			процесс. Вы верно учли следующие затраты:
		</div>
		<div class="container__list">
			<ul>
				<li v-for="item in list">{{ item }}</li>
			</ul>
		</div>
		<div class="text" v-if="resultOne">
			Итоговая сумма затрат по данному несчастному случаю составила
			<span class="bold">19 135 500 руб</span>. Пройдите тренажер еще раз, чтобы
			закрепить свои знания и отработать расчет экономических потерь
			работодателя от травматизма.
		</div>
		<div class="text" v-else-if="resultTwo">
			Итоговая сумма затрат по данному несчастному случаю должна составить
			<span class="bold">19 135 500 руб</span>. Внимательно изучите условие
			задачи еще раз и попробуйте верно рассчитать экономические потери от
			несчастного случая.
		</div>
		<div class="text" v-else-if="resultThree">
			Итоговая сумма затрат по данному несчастному случаю должна составить
			<span class="bold">19 135 500 руб</span>. Внимательно изучите условие
			задачи еще раз и попробуйте верно рассчитать экономические потери от
			несчастного случая.
		</div>
		<div class="text" v-else-if="resultFour">
			Что касается заработной платы не выплаченной пострадавшему, этот
			показатель не учитывают. Так как работодатель во время больничного
			не платит пострадавшему зарплату.
			<div class="mt05">
				Итоговая сумма затрат по данному несчастному случаю должна составить
				<span class="bold">19 135 500 руб</span>. Внимательно изучите условие
				задачи еще раз и попробуйте верно рассчитать экономические потери от
				несчастного случая.
			</div>
		</div>
		<div v-else>
			Совсем не так. Вы неверно выбрали затраты, которые понес работодатель
			из-за несчастного случая с оператором станка. Внимательно изучите условие
			задачи еще раз и попробуйте верно рассчитать экономические потери.
		</div>
		<div class="container__btn">
			<Button @click="reload">Посчитать экономические затраты еще раз</Button>
		</div>
	</div>
</template>

<style scoped>
