<script setup>
import { ref } from 'vue'
import Button from './Button.vue'

const props = defineProps({
	expenses: {
		type: Array,
		required: true,
	},
})

const emit = defineEmits(['nextStep'])

const userChoose = ref([])

const handleClick = () => {
	emit('nextStep', userChoose.value)
}
</script>

<template>
	<div class="container">
		<div class="container__check">
			<label
				v-for="(expense, index) in expenses"
				:key="index"
				class="checkbox-label"
			>
				<input type="checkbox" :value="expense.name" v-model="userChoose" />
				{{ expense.name }}
			</label>
		</div>
		<div v-if="userChoose.length > 0" class="container__bnt">
			<Button @click="handleClick">Посчитать экономические потери</Button>
		</div>
	</div>
</template>

<style scoped>
.container {
	padding: 20px;
}

.container__check {
	display: flex;
	flex-direction: column;
}

.checkbox-label {
	margin-bottom: 1.5vw;
	font-size: 1.2em;
	cursor: pointer;
	display: grid;
	grid-template-columns: 0.05fr 1fr;
	justify-content: center;
	align-items: center;
}

input[type='checkbox'] {
	cursor: pointer;
}

.container__bnt {
	margin-top: 2vw;
	display: flex;
	justify-content: center;
}
</style>
