<script setup>
import { ref, computed } from 'vue'
import Button from './UI/Button.vue'

const props = defineProps({
	expenses: {
		type: Array,
		required: true,
	},
})

const emit = defineEmits(['result'])

// Состояние для хранения текущей активной подсказки и её позиции
const activehit = ref(null)
const hintPosition = ref({ x: 0, y: 0 })

// Функция для форматирования числа с разделителем тысяч
const formatNumber = value => {
	return new Intl.NumberFormat('ru-RU').format(value)
}

// Функция для преобразования строки с разделителем в число
const parseNumber = value => {
	return parseFloat(value.replace(/\s/g, '')) || 0
}

// Вычисляемое свойство для общей суммы
const total = computed(() => {
	return props.expenses.reduce((sum, item) => sum + item.value, 0)
})

// Обработчик изменения значения
const changeValue = (e, item) => {
	let rawValue = e.target.value
	rawValue = rawValue.replace(/[^\d]/g, '')
	const parsedValue = parseNumber(rawValue) || 0
	item.value = parsedValue
	e.target.value = formatNumber(parsedValue)
}

// Обработчик клика по изображению
const togglehit = (item, event, index) => {
	if (activehit.value === item.name) {
		// Если подсказка уже активна, скрываем её
		activehit.value = null
	} else {
		// Иначе показываем подсказку для текущего элемента
		activehit.value = item.name
		// Получаем координаты курсора
		hintPosition.value = {
			x: event.clientX,
			y: event.clientY,
			isLast: index === props.expenses.length - 1, // Проверяем, последний ли это элемент
		}
	}
}

const handleClickResult = () => {
	emit('result', total.value)
}
</script>

<template>
	<div class="container">
		<div class="container__table">
			<table>
				<thead>
					<tr>
						<th>Затраты работодателя</th>
						<th>Сумма затрат</th>
					</tr>
				</thead>
				<tbody>
					<tr v-for="(item, index) in expenses" :key="item.name">
						<td>
							{{ item.name }}
							<span
								v-if="item.hit !== undefined"
								@click="togglehit(item, $event, index)"
							>
								<img src="../assets/lightbulb.svg" alt="" />

								<div
									v-if="activehit === item.name"
									class="hit"
									:class="{ 'hit-top': hintPosition.isLast }"
									:style="{
										left: `${hintPosition.x}px`,
										top: `${hintPosition.y}px`,
									}"
								>
									{{ item.hit }}
								</div>
							</span>
						</td>
						<td class="narrow-column">
							<input
								@input="e => changeValue(e, item)"
								:id="item.name"
								type="text"
								class="styled-input"
								:value="formatNumber(item.value)"
							/>
						</td>
					</tr>
				</tbody>
			</table>

			<div class="container__total">
				Итоговая сумма затрат: {{ formatNumber(total) }} руб
			</div>

			<div v-if="total > 0" class="container__btn">
				<Button @click="handleClickResult">Проверить</Button>
			</div>
		</div>
	</div>
</template>

<style scoped>
.container {
	padding: 1vw;
}

table {
	width: 100%;
	border-collapse: collapse;
	font-size: 1em;
}

table,
th,
td {
	border: 1px solid black;
}

th,
td {
	padding: 1vw;
}

th {
	font-weight: bold;
}

td {
	text-align: left;
	position: relative; /* Для позиционирования подсказки */
}

.styled-input {
	width: 100%;
	padding: 0.5vw;
	font-size: 1em;
	border: 0.3vw solid #ccc;
	outline: none;
	transition: border-color 0.3s;
	appearance: textfield;
}

.container__total {
	text-align: right;
	margin-top: 2vw;
	font-weight: 900;
}

.narrow-column {
	width: 20vw;
}

img {
	width: 3.5vw;
	cursor: pointer;
	transition: scale 0.3s;
}

img:hover {
	scale: 1.07;
}

.hit {
	position: fixed; /* Подсказка будет позиционироваться относительно окна браузера */
	background-color: #f9f9f9;
	border: 1px solid #ccc;
	padding: 1vw;
	border-radius: 1vw;
	box-shadow: 0 2px 5px rgba(0, 0, 0, 0.1);
	z-index: 10;
	max-width: 50vw;
	transform: translate(-50%, 3vw);
	text-align: justify;
}

.hit-top {
	transform: translate(-50%, calc(-100% - 3vw)); /* Подсказка сверху */
}

.container__btn {
	margin-top: 2vw;
	display: flex;
	justify-content: center;
}
</style>
